}

h2 {
  padding: unset;
  margin-top: 1rem;
  margin-bottom: .3rem;
}

h3, h4 {
  margin-top: .2rem;
  margin-bottom: .2rem;
}

.container {
  width: 90%;
  position: center;
  margin: auto;
  margin-left: auto;
  margin-right: auto;
}

