  text-align: center;
}

.library {
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: grid;
}

.actions {
  height: 3rem;
  background-image: linear-gradient(33deg, rgba(176, 38, 255, .75), rgba(38, 255, 237, .75));
  border: 1px solid #fff;
  border-radius: 5px 10px;
