  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
}

.Book {
  width: 100%;
  color: #fff;
  background-image: linear-gradient(33deg, rgba(176, 38, 255, .75), rgba(38, 255, 237, .75));
  border: 1px solid #fff;
  grid-template-rows: 1fr 1fr;
  padding-left: 1rem;
  padding-right: 1rem;
  display: grid;
}

.Book:nth-child(2n) {
  border-radius: 5px 50px 5px 10px;
}

.Book:nth-child(2n+1) {
  border-radius: 50px 5px 10px;
}

.Book .buttons {
  height: 1.5rem;
  margin-bottom: .2rem;
  display: flex;
}

.Book input {
  width: 45%;
}

input {
  text-align: justify-all;
